.mh_home_page_container {
  .mh_home_page_content {
    align-items: center;
    display: flex;
    flex-direction: column;
    margin-bottom: 36px;
  }

  .background_home {
    position: absolute;
    top: 0;
    bottom: auto;
    left: 0;
    right: 0;
    z-index: -1;
  }

  .mh_home_page_about {
    margin-top: 46px;
    width: 85%;
    position: relative;
    .flowerUpper_section {
      position: absolute;
      top: -14px;
      left: -133px;
      object-fit: cover;
      width: 182px;
      transform: rotate(8deg);
      .upper-image {
        // position: absolute;
        // left: -194px;
        // top: -88px;
        width: 212px;
        height: 412px;
        object-fit: contain;
      }
    }

    .about_title {
      font-size: 2.5rem;
      font-weight: 900;
      font-family: 'Google Sans', sans-serif;
    }
    .about_content {
      margin-top: 18px;
      font-size: 20px;
      font-weight: 400;
      font-family: 'Google Sans', sans-serif;
    }
    .banner_home_about1 {
      margin-top: 18px;
      width: 500px;
      height: 256px;
    }
    @media only screen and (max-width: 800px) {
      .menu_item_content {
        flex-direction: column;
      }
    }
    @media only screen and (max-width: 1280px) {
      .banner_home_about1 {
        // height: 5vw;
      }
    }
    @media only screen and (max-width: 600px) {
      .banner_home_about1 {
        margin-top: 5px;
        width: 100%;
        height: 200px;
      }
    }
  }
  .mh_home_page_urbanbbq_title {
    width: 85%;
    margin-top: 36px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    .title {
      font-size: 2.5rem;
      font-weight: 900;
      font-family: 'Google Sans', sans-serif;
    }
  }

  .mh_home_page_daohua_title {
    width: 85%;
    margin-top: 36px;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    .title {
      font-size: 2.5rem;
      font-weight: 900;
      font-family: 'Google Sans', sans-serif;
    }
  }

  .mh_home_page_daohua_voucher {
    z-index: 1;
    position: relative;
    width: 85%;
    margin-top: 36px;
  }

  .mh_home_page_urbanbbq_voucher {
    width: 85%;
    margin-top: 36px;
    margin-bottom: 36px;
  }

  .voucher_section {
    position: relative;
    .lower-image {
      z-index: 0;
      position: absolute;
      right: -38px;
      bottom: -104px;
    }
  }
  .banner_home_daohua {
    margin-top: 36px;
    width: 100%;
    height: 33vw;
  }
  .banner_home_urban {
    margin-top: 36px;
    width: 100%;
    height: 33vw;
  }
  .mh_home_page {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  .see_all {
    color: #b99c31;
  }

  @media only screen and (max-width: 600px) {
    .mh_home_page_urbanbbq_title {
      width: 95%;
    }

    .mh_home_page_daohua_title {
      width: 95%;
    }
    .mh_home_page_daohua_voucher {
      width: 95%;
    }

    .mh_home_page_urbanbbq_voucher {
      width: 95%;
    }

    .mh_home_page_about {
      width: 95%;
      margin-top: 10px;
    }

    .banner_home_urban {
      height: 180px;
      width: 100%;
    }

    .banner_home_daohua {
      height: 180px;
      width: 100%;
    }
  }
}
