:root,
[data-theme='light'] {
  --black: #000000;
  --white: #ffffff;
  //background
  --header-bg: #35ccd9;
  --footer-bg: #00adbb;
  --page-bg: #f5f6f8;
  --content-bg: #e5e5e5;

  // text
  --text-primary: #3a3a3c;
  --text-secondary: #00adbb;
  --text-placeholder: #bcbec0;
  --text-grey: #505f79;
  --text-dark-blue: #1b315f;

  // colors
  --primary-blue: #00adbb;
  --primary-orange: #ef7f30;
  --primary-yellow: #f2c94c;
  --primary-gray: #495057;
  --primary-red: #fa5252;
  --secondary-blue: #0674e8;
  --secondary-red: #d34836;
  --primary-pink: #fe5466;
  --primary-peach: #e8b4be;
  --primary-gray-blur: #adb1b9;
  --primary-yellow-dark: #c4a732;
  --primary-gray-green: #37c86f;

  --border-secondary: #c4c4c4;
  --line-pink: #eabfb9;

  //icon
  --icon-color: #a6aebb;
}
