@import 'src/styles/base';
.recruitment_home_item_container {
  .thumbnail {
    height: 100%;
    width: 100%;
    object-fit: cover;
  }
  .image-section {
    border-radius: 8px;
    overflow: hidden;
    height: 237px;
    position: relative;

    .content-image-section {
      display: flex;
      justify-content: space-between;
      height: 100%;
      width: 100%;
      align-items: flex-end;
      padding: 16px;

      .content-eVoucher {
        height: 100%;
        display: flex;
        flex-direction: column;

        .title-eVoucher {
          padding-top: 16px;
          color: white;
          font-size: 18px;
        }
        .eVoucher-value {
          padding: 12px 0 12px 0;
          font-size: 36px;
          font-weight: bold;
          background: -webkit-linear-gradient(45deg, #1b315f, #4e628b 80%);
          -webkit-background-clip: text;
          -webkit-text-fill-color: transparent;
        }
        .eVoucher-value-string {
          color: white;
          font-size: 18px;
        }
      }

      .percent-item {
        display: flex;
        justify-content: center;
        align-items: center;
        background-color: #fe5466;
        width: 40px;
        height: 40px;
        border-radius: 50%;

        .percent-value {
          color: white;
        }
      }
    }
  }

  .created_date_box {
    border: 4px solid #b99c31;
    width: 111px;
    height: 111px;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
  }
  .created_day {
    color: #b99c31;
    font-size: 36px;
    font-family: 'Google Sans', sans-serif;
    font-weight: 700;
  }
  .created_month {
    color: #b99c31;
    font-size: 36px;
    font-family: 'Google Sans', sans-serif;
    font-weight: 300;
  }

  .eVoucher-name {
    height: 50px;
    white-space: nowrap;
    text-overflow: ellipsis;
    overflow: hidden;
    font-size: 18px;
    font-family: 'Google Sans', sans-serif;
    font-weight: 700;
  }
  .eVoucher-description {
    display: -webkit-box;
    -webkit-line-clamp: 2; /* number of lines to show */
    -webkit-box-orient: vertical;
    white-space: nowrap;
    text-overflow: ellipsis;
    color: #b99c31;
    font-size: 17px;
    overflow: hidden;
    font-family: 'Google Sans', sans-serif;
    font-weight: 400;
  }

  .bottom-section {
    display: flex;
    justify-content: space-between;

    .bottom-left {
      display: flex;
      align-items: center;
      .price {
        color: #ff4355;
        font-size: 18px;
        padding-left: 8px;
      }
    }

    .bottom-right {
      .customTitle {
        font-size: 17px;
        font-weight: bold;
      }
    }
  }
}
